<template>
  <table class="table m-0 table-top-line-none" v-if="members.length > 0">

    <tr v-for="(v, index) in members" :key="v.id">

      <td width="4%" class="text-accent">{{ index + 1 }}</td>

      <td width="8%" >{{ v.num }}</td>

      <td width="13%">{{ v.named }}</td>
      <td width="10%" >{{ v.contact_named }}</td>
      
      <td width="15%" >
        <div v-if="v.area != null">{{ v.area.area }}&nbsp;{{ v.phoned }}</div>
      </td>
      <td width="10%" class="text-center">

        <div class="tag tag-table tag-table-center"  v-if="v.tag != null"
          :style="{ 'background': v.tag.color }">
          {{ v.tag.named }}
        </div>

      </td>
      <td width="30%" >{{ v.addr }}</td>
      
      <td width="10%">

        <i @click="option(v.id, 'trash')" class="float-right">
          <remove></remove>
        </i>

        <i @click="option(v.id, 'update')" class="float-right">
          <pencil></pencil>
        </i>

      </td>
    </tr>

  </table>
  <empty v-else></empty>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'
import Loading from '@/components/Shimmer/Loading.vue'
import Empty from '@/components/Shimmer/Empty.vue'

export default {
  name: 'MemberViewBody',
  components: { 
    Pencil, Remove,
    Loading,
    Empty  
  },
  props: {
    members: {
      type: Array
    }
  },
  methods: {
    option(id, option) {
      this.$emit('option_Father' , id, option)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


